<script setup>
    import List_box from '../components/List_box.vue';
    import top from '../components/_top.vue'
    import bottom from '../components/_bottom.vue';
    import hamburger_menu from '../components/hamburger_menu.vue';
    import return_button from '../components/return_button.vue';
    import warning_component from '../components/warning-component.vue'
    import draggableComponent from 'vuedraggable';
    import { gen_listdata, onload_dataInput } from '../scripts/gen_listdata';
    import { defineEmits } from 'vue';
    import { ref , onMounted, onUpdated } from 'vue'

    //emit定義
    const emit = defineEmits(['change_page']);

    let active_list_number = ref(1)             //アクティブなリストデータを指定する変数

    let disp_warn = ref(false)                  //警告画面の表示状態

    let AutoScroll_data = ref(false)             //_topのトグルのデータを管理する変数
    let ScrollRev_data  = ref(false)

    let btn_clicked_value_mainPage = ref(false) //warn_comp内のボタンをクリックしたか
    let selected_decision_condit = ref(false)     //warn_comp内の決定ボタンを押したか

    const content_nop = ref(gen_listdata(7))     //リスト生成用のデータ

    //hamburger_menuのボタンの状態の受取とメニュー本体への指示送信
    // temp_hm_b_condit App.vue内で使用する受け渡しを行う一時的な変数
    var temp_hm_b_condit = ref(false)
    const hamburger_menu_condit = (hm_b_condit) => {
        temp_hm_b_condit.value = hm_b_condit
    }

    const change_list_data = (change_list_data) =>{
        active_list_number.value = change_list_data
    }

    const toggle_changed_relay = (toggle_data, toggle_checked) =>{
        if (toggle_data == "AutoScroll"){
          AutoScroll_data.value = Boolean(toggle_checked)
        } else {
          ScrollRev_data.value  = Boolean(toggle_checked)
        }
    }

    //warning-component関係
    const warn_popup_anim = (condition) => {
      if ( condition == "confirm"){
        selected_decision_condit.value = true;
        btn_clicked_value_mainPage.value = true;
      } else {
        btn_clicked_value_mainPage.value = true;
      }
    }

    const warn_anim_end = ( condition ) => {
      if ( condition == "dalete_cache" ){
        disp_warn.value = false;
        btn_clicked_value_mainPage.value = false;
        selected_decision_condit.value = false;
        localStorage.clear()
      } else {
        disp_warn.value = false;
        btn_clicked_value_mainPage.value = false;
      }
    }

    const emit_change_page = (page_data) => {
      if (page_data != "clear_device_data"){
        emit("change_page", page_data);
      } else {
        disp_warn.value = true;
      }
    }

    onMounted(() =>{
      onload_dataInput(active_list_number.value)
    });

    onUpdated(() =>{
      onload_dataInput(active_list_number.value)
    });
</script>

<template>
    <warning_component :content="'端末に保存されているリストの内容などの\n[すべて]のデータを削除します。\n削除する場合は、\n削除するを5回タップしてください。\n\n※削除後のデータは戻せません。\n削除実行後、現在のリストの内容は消えます。'" :confirm="'削除する'" :selected_decision="selected_decision_condit" :btn_clicked_value="btn_clicked_value_mainPage" @warning_btn_clicked="warn_popup_anim" @warn_anim_end="warn_anim_end" v-if="disp_warn == true"/>
    <top @hamburgerMenu_condition="hamburger_menu_condit" @change_list_number="change_list_data" @toggle_changed="toggle_changed_relay" :active_list_number="active_list_number"/>
    <hamburger_menu :hm_b_condit="temp_hm_b_condit" @change_page="emit_change_page"/>
    <div id="_main_">
      <ul>
        <!--- 要リファクタリング（更にコンポーネント化） --->
        <draggableComponent v-model="content_nop[0]" item-key="id" :handle="'.line_header'" :animation="300">
          <template v-slot:item="{ element }">
            <List_box :component_ids="element.ids" :load_top_id="element.top_id" :load_btm_id="element.btm_id" :AutoScroll="AutoScroll_data" :ScrollRev="ScrollRev_data" v-if="active_list_number == 1"/>
          </template>
        </draggableComponent>
        <draggableComponent v-model="content_nop[1]" item-key="id" :handle="'.line_header'" :animation="300">
          <template v-slot:item="{ element }">
            <List_box :component_ids="element.ids" :load_top_id="element.top_id" :load_btm_id="element.btm_id" :AutoScroll="AutoScroll_data" :ScrollRev="ScrollRev_data" v-if="active_list_number == 2"/>
          </template>
        </draggableComponent>
        <draggableComponent v-model="content_nop[2]" item-key="id" :handle="'.line_header'" :animation="300">
          <template v-slot:item="{ element }">
            <List_box :component_ids="element.ids" :load_top_id="element.top_id" :load_btm_id="element.btm_id" :AutoScroll="AutoScroll_data" :ScrollRev="ScrollRev_data" v-if="active_list_number == 3"/>
          </template>
        </draggableComponent>
        <draggableComponent v-model="content_nop[3]" item-key="id" :handle="'.line_header'" :animation="300">
          <template v-slot:item="{ element }">
            <List_box :component_ids="element.ids" :load_top_id="element.top_id" :load_btm_id="element.btm_id" :AutoScroll="AutoScroll_data" :ScrollRev="ScrollRev_data" v-if="active_list_number == 4"/>
          </template>
        </draggableComponent>
        <draggableComponent v-model="content_nop[4]" item-key="id" :handle="'.line_header'" :animation="300">
          <template v-slot:item="{ element }">
            <List_box :component_ids="element.ids" :load_top_id="element.top_id" :load_btm_id="element.btm_id" :AutoScroll="AutoScroll_data" :ScrollRev="ScrollRev_data" v-if="active_list_number == 5"/>
          </template>
        </draggableComponent>
        <draggableComponent v-model="content_nop[5]" item-key="id" :handle="'.line_header'" :animation="300">
          <template v-slot:item="{ element }">
            <List_box :component_ids="element.ids" :load_top_id="element.top_id" :load_btm_id="element.btm_id" :AutoScroll="AutoScroll_data" :ScrollRev="ScrollRev_data" v-if="active_list_number == 6"/>
          </template>
        </draggableComponent>
        <draggableComponent v-model="content_nop[6]" item-key="id" :handle="'.line_header'" :animation="300">
          <template v-slot:item="{ element }">
            <List_box :component_ids="element.ids" :load_top_id="element.top_id" :load_btm_id="element.btm_id" :AutoScroll="AutoScroll_data" :ScrollRev="ScrollRev_data" v-if="active_list_number == 7"/>
          </template>
        </draggableComponent>
      </ul>
      <return_button :ScrollRev="ScrollRev_data"/>
    </div>
    <bottom/>
</template>
