const easeOutQuad = (t) => 1 - (1 - t) * (1 - t);

const smoothScrollTo = (targetY, duration = 1000) => {
    const startY = window.scrollY;
    const difference = targetY - startY;
    const startTime = performance.now();

    const step = (currentTime) => {
        const elapsedTime = currentTime - startTime;
        const progress = Math.min(elapsedTime / duration, 1);
        window.scrollTo(0, startY + difference * easeOutQuad(progress));

        if (progress < 1) {
            requestAnimationFrame(step);
        }
    };

    requestAnimationFrame(step);
};

const smoothScrollToCenterWithMemory = (targetElement, duration = 1000) => {
    if (!targetElement) return window.scrollY; // スクロール前の位置を返す

    const elementRect = targetElement.getBoundingClientRect();
    const targetY = window.scrollY + elementRect.top - (window.innerHeight / 2) + (elementRect.height / 2);

    smoothScrollTo(targetY, duration);
    return window.scrollY; // スクロール前の位置を返す
};

export { smoothScrollTo, smoothScrollToCenterWithMemory };
