<script setup>
import main_app from "./pages/main_app.vue"
import restore_data from "./pages/restore_data.vue";
import { ref } from 'vue'


let display_data = ref("main_app")

const back_page_func = (page) => {
  display_data.value = page
}

const change_page = (page_data) => {
  display_data.value = page_data
}

</script>

<template>
  <main_app @change_page="change_page" v-if="display_data == 'main_app'" />
  <restore_data @page_change="back_page_func" v-if="display_data == 'restore_data'" />
</template>

