export function gen_listdata(number){
    var datas = []
    for (var p = 0; p < number; p ++){
        datas.push([])
    }
    
    //リストデータの生成
    for (let content_block = 0; content_block < number; content_block++){
        for(var i = 1; i < 41; i++){
            datas[content_block].push({id:i , ids:String(content_block + 1) + "-" +String(i) , top_id:String("top_"+(content_block + 1)+"-"+i) , btm_id:String("btm_"+(content_block + 1)+"-"+i)})
        }
    }

    return datas;
}

//リスト内部の値の取得とセット
export function onload_dataInput(data_id){
    const data_pos = ["top_","btm_"]  //初期値取得のための用語
    for (var form_block_elements = 1; form_block_elements < 41; form_block_elements++){
        for(var input_elements = 0; input_elements < 2; input_elements++){
            var target_data_id = String(data_pos[input_elements] + data_id + "-" +form_block_elements)
            var target_element = document.getElementById(target_data_id)
            if (localStorage.getItem( target_data_id ) !== null){
                target_element.value = localStorage.getItem( target_data_id )
            } else {
                target_element.value = ""
            }
        }
    }
}