<template>
        <button class="menu-btn _button_position_close" id="button_body" @click="btnAction">
            <span class="menu-btn __line _top" :class="{ is_open : hm_b_condit }"></span>
            <span class="menu-btn __line _bottom" :class="{ is_open : hm_b_condit }"></span>
        </button>
</template>

<script>

//hm_b_condit == hamburgermenu_button_condition

export default {
    data() {
        return {
            hm_b_condit : false,
        }
    },
    methods: {
        btnAction() {
            this.hm_b_condit = !this.hm_b_condit
            button_position_update(this.hm_b_condit)
            this.$emit('hamburgerMenu_condition', this.hm_b_condit)
        }
    }
}

function button_position_update( condition ){
    const button_body_element = document.getElementById("button_body")
    if ( condition == true ){
        button_body_element.classList.replace("_button_position_close", "_button_position_open")
    } else {
        button_body_element.classList.replace("_button_position_open", "_button_position_close")
    }
}
</script>

<style>
.menu-btn {
    z-index: 5;
    display:block;
    width:50px;
    height:50px;
    box-shadow:0px 0px 20px rgba(0,0,0,.15);
    background:rgb(62, 62, 62);
    position:absolute;
    top:50px;
    left:40px;
    transform:translate(-50%,-50%);
    cursor:pointer;
    border-radius: 50px;
    border: none;
    transition: .3s;
    -webkit-tap-highlight-color:rgba(0,0,0,0);
    cursor:pointer;
    
    .__line {
        pointer-events: none;
        display:block;
        width:60%;
        height:3px;
        background-color:#ffffff;
        position:absolute;
        left:50%;
        transform:translateX(-50%); 
        &._top {
            top:15px;
            &.is_open {
                top:23px;
                transform:translateX(-50%) rotate(40deg);
            }
        }
        &._bottom {
            top:30px;
            &.is_open {
                top:23px;
                transform:translateX(-50%) rotate(-40deg);
            }
        }
    }
}

.menu-btn:hover{
    background:rgb(143, 143, 143);
}

.menu-btn:active{
    background:rgb(17, 17, 17);
    box-shadow: 0px 3px 3px 3px rgba(0, 0, 0, 0.4);
}

._button_position_close{
    position: absolute;
}

._button_position_open{
    position: fixed;
}

</style>