<template>
    <button :class="{ 'invisible':this.invisibleMode == true }" id="return" @click="return_pos()"><span :class="{ 'triangle':this.ScrollRev == false, 'triangleRev':this.ScrollRev == true }"></span></button>
</template>

<style>
#return{
    position: fixed;
    bottom: 30px;
    right: 10px;
    background: rgb(255, 100, 28);
    width: 60px;
    height: 60px;
    font-size: 2rem;
    border: none;
    border-radius: 5px;
    box-shadow: -1px 7px 10px 0.1px rgba(255, 255, 255, 0.537);
    transition: .3s;
}

.invisible{
    display: none;
}

.triangleRev{
    display: inline-block;
    vertical-align: top;
    color: #ffffff;
    line-height: 0px;
    width: .7em;
    height: .7em;
    border: 0.2em solid currentColor;
    border-left: 0;
    border-bottom: 0;
    box-sizing: border-box;
    border-radius: 5px;
    transform: translateY(25%) rotate(-225deg);
}

.triangle {
    display: inline-block;
    vertical-align: middle;
    color: #ffffff;
    line-height: 1;
    width: .7em;
    height: .7em;
    border: 0.2em solid currentColor;
    border-left: 0;
    border-bottom: 0;
    box-sizing: border-box;
    border-radius: 5px;
    transform: translateY(25%) rotate(-45deg);
}

#return:active{
    transform: translateY(10px);
    box-shadow:none;
}
</style>

<script>
import { smoothScrollTo } from '../scripts/scroller';



export default {
    props: {
        ScrollRev: Boolean
    },
    data: function() {
        return {
            invisibleMode: true
        }
    },
    methods: {
        return_pos(){
            if (this.ScrollRev == false){
                smoothScrollTo( 0, 300 )
            } else {
                smoothScrollTo( 10109, 300 )
            }
            
        },
        handleScroll() {
            this.invisibleMode = this.ScrollRev 
            ? window.scrollY >= 9109 
            : window.scrollY <= 386;
        }
    },
    mounted() {
        window.addEventListener('scroll', this.handleScroll);
    },
}
</script>