<template>
    <div class="menu_close menu_base" id="hamburger_menu">
        <div class="user_name">
        </div>
        <div class="data_controller">
            <h3 class="section_title">リストコントロール(未実装)</h3>
            <ul class="list_data_restore_backup">
                <li><a>データのリストア</a></li>
                <li><a>データのバックアップ</a></li>
            </ul>
        </div>
        <div class="device_data_controll">
            <h3 class="section_title">データコントロール</h3>
            <ul class="device_data_controll_sections">
                <li><a v-on:click="clear_device_data">端末内データの削除</a></li>
            </ul>
        </div>
    </div>
</template>

<script>
import { watch } from 'vue';

export default {
    props: {
        hm_b_condit: Boolean
    },
    methods:{
        restore_data: function() {
            this.$emit("change_page","restore_data")
        },
        clear_device_data: function() {
            this.$emit("change_page", "clear_device_data")
        }
    },
    setup(props) {
    watch(
        () => props.hm_b_condit,
        (newValue) => {
            Menu_Motion(newValue)
        }
    );
        return {};
    }
};

function Menu_Motion(condition){
    const html_menu_element = document.getElementById("hamburger_menu")
    if ( condition == true ){
        html_menu_element.classList.replace("menu_close", "menu_open")
    } else {
        html_menu_element.classList.replace("menu_open", "menu_close")
    }
}
</script>

<style>
.menu_base{
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    z-index: 4;
    background: rgb(80, 80, 80);
    transition: .2s;
}

.menu_close{
    transform: translateX(-100%);
}

.menu_open{
    transform: translateX(0%);
}

.user_name{
    height: 50px;
    position: relative;
    top: 25px;
}

#disp_user_name{
    color: rgb(255, 255, 255);
    position: relative;
    top: 50%;
    width: 70%;
    left: 75px;
    background: rgb(66, 37, 117);
    padding: 5px 15px;
    border-radius: 10px;
    margin: 0;
    text-align: center;
    transform: translateY(-50%);

}

.data_controller, .account_service, .device_data_controll{
    position: relative;
    color: white;
    width: 90%;
    margin: 0 auto;
    margin-top: 50px;
}

.section_title{
    background: rgb(0, 140, 255);
    padding: 6px 20px;
    border-radius: 10px;
    text-align: center;
    margin-bottom: 0px;
    margin-top: 20px;
}

.list_data_restore_backup, .device_data_controll_sections, .account_controlls{
    width: 100%;
    margin: 0;
}

.list_data_restore_backup a, .device_data_controll_sections a, .account_controlls a{
    position: relative;
    color: white;
    display: block;
    text-decoration:none;
    font-size: 1.2rem;
    margin: 0;
    text-align: center;
    margin: 0 auto;
    width: 80%;
    background: rgb(43, 43, 43);
    border-radius: 5px;
    padding: 10px 0;
    margin-top: 10px;
    box-shadow: 0px 10px 10px -5px rgba(255, 255, 255, 0.347);
    transition: .1s;
    -webkit-tap-highlight-color:rgba(0,0,0,0);
    cursor:pointer;
}

.list_data_restore_backup a:active, .device_data_controll_sections a:active, .account_controlls a:active{
    transform: translateY(3px);
    background: rgb(75, 75, 75);
    box-shadow: none;
}

</style>
