<template>
    <label class="toggle-button">
        <input @click="checked()" type="checkbox"/>
    </label>
</template>

<style>
.toggle-button {
    -webkit-tap-highlight-color:rgba(0,0,0,0);
    cursor:pointer;
    display: inline-block;
    position: relative;
    width: 60px;
    height: 30px;
    border-radius: 50px;
    border: 3px solid #dddddd;
    box-sizing: content-box;
    cursor: pointer;
    transition: border-color .1s;
}

.toggle-button:has(:checked) {
    border-color: #4bd865;
}

.toggle-button::after {
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #dddddd;
    content: '';
    transition: left .1s;
}

.toggle-button:has(:checked)::after {
    left: 30px;
    background-color: #4bd865;
}

.toggle-button input {
    display: none;
}
</style>

<script>

export default{
    props: {
        toggle_data:String
    },
    data() {
        return{
            toggle_checked: false
        }
    },
    methods:{
        checked(){
            this.toggle_checked = !this.toggle_checked

            let toggle_cont_data = String(this.toggle_data)
            let toggle_changed = this.toggle_checked
            this.$emit("toggle_changed", toggle_cont_data, toggle_changed)
        }
    }
}
</script>