<template>
    <li>
        <button class="list_select_button_styles" @click="clicked">リスト{{ this_button_list_number }}</button>
    </li>
</template>

<script>
export default{
    props: {
        this_button_list_number: String
    },
    methods: {
        clicked() {
            this.$emit('change_list_number', Number(this.this_button_list_number))
        }
    }
}

var isTouch = false;

document.addEventListener('touchstart', function(e) {
    if (e.target.type === 'button') {
        isTouch = true;
    }
}, { passive: true });

document.addEventListener('touchmove', function(e) {
    if (e.target.type === 'button') {
        isTouch = false;
    }
}, { passive: true });

document.addEventListener('touchend', function(e) {
    if (e.target.type === 'button') {
        if (isTouch) {
            e.target.click();
            e.preventDefault();
        }
    }
});
</script>

<style>
.list_select_button_styles{
    background: rgb(0, 74, 201);
    color: white;
    border: none;
    width: 150px;
    margin: 0 10px;
    height: 40px;
    font-size: 1.1rem;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0px 6px 6px 0px rgba(255, 255, 255, 0.366);
    transition: .2s;
}

.list_select_button_styles:active{
    transform: translateY(2px);
    background: rgb(0, 42, 113);
    box-shadow:none;
}
</style>