<script>
import hamburger_button from './hamburger_button.vue';
import toggle_switch from './toggle_switch.vue';
import list_button from './list_button.vue';
import { ref } from 'vue';

//hm_b_condit == hamburgermenu_button_condition

//一時的な変数（データ格納用）
let list_contents_button = []

for (let i = 1; i < 8; i++){
    list_contents_button.push({id:i, this_button_list_number:String(i)})
}

//最終的に使用される変数
let contents_button = ref(list_contents_button)

//トグルに渡す役割のデータ
let toggle_data = ref([["AutoScroll"],["ScrollRev"]])

export default{
    components: {
        hamburger_button,
        toggle_switch,
        list_button
    },
    data() {
        return{
            active_list_number : 1,
            hm_b_condit : false,
            contents_button,
            toggle_data,
        };
    },
    methods: {
        relay_data_hm_condit(hm_b_condit){
            this.hm_b_condit = hm_b_condit
            this.$emit('hamburgerMenu_condition', this.hm_b_condit)
        },
        relay_data_list_number(change_list_data){
            this.active_list_number = change_list_data
            this.$emit('change_list_number', change_list_data)
        },
        chkbox_autoreset(){
            for(let i = 0; i < 40; i++){
                document.getElementsByClassName('check_button')[i].checked = false;
            }
        },
        toggle_changed_relay(toggle_data, toggle_checked){
            this.$emit("toggle_changed",toggle_data, toggle_checked)
        }
    }
}

</script>

<template>
    <div class="top">
        <h3 class="list_no">現在のリスト：{{ active_list_number }}番</h3>
        <hamburger_button @hamburgerMenu_condition="relay_data_hm_condit"/>
        <div class="auto_scroll">
            <h2 class="mini_title">自動スクロール</h2>
            <toggle_switch class="toggle" :toggle_data="String(toggle_data[0])" @toggle_changed="toggle_changed_relay"/>
        </div>
        <div class="auto_scroll_inversion">
            <h2 class="mini_title">スクロール反転</h2>
            <toggle_switch class="toggle" :toggle_data="String(toggle_data[1])" @toggle_changed="toggle_changed_relay"/>
        </div>
        <div class="check_all_release">
            <h2 class="mini_title">チェック全解除</h2>
            <button @click="chkbox_autoreset">解除</button>
        </div>
        <div>
            <ul class="list_buttons">
                <list_button v-for="content in contents_button" :key="content.id" :this_button_list_number="content.this_button_list_number" @change_list_number="relay_data_list_number"/>
            </ul>
        </div>
    </div>
</template>

<style>
.list_no{
    position: relative;
    float: right;
    border-radius: 5px;
    padding: 10px 0;
    top: 30px;
    right: 20px;
    margin: 0;
    width: 75%;
    text-align: center;
    color: white;
    background: rgb(165, 46, 178);
}

.mini_title{
    font-size: 1.5rem;
}

.toggle{
    top: -15px;
}

.top{
    position: relative;
    top: 0;
    width: 100%;
    height: 380px;
    background: rgb(62, 62, 62);
}

.auto_scroll{
    position: absolute;
    top: 60px;
    left: 10px;
    margin: 0;
    width: fit-content;
    color: white;
}
.auto_scroll_inversion{
    position: absolute;
    top: 60px;
    left: 200px;
    margin: 0;
    width: fit-content;
    color: white;
}

.check_all_release{
    position: absolute;
    top: 160px;
    left: 20px;
    margin: 0;
    width: fit-content;
    color: white;
}

.check_all_release button{
    position: relative;
    top: -10px;
    background-color: red;
    border: none;
    width: 150px;
    height: 40px;
    border-radius: 10px;
    color: white;
    font-size: 20px;
    transition: .1s;
}
.check_all_release button:active{
    background-color: rgb(135, 0, 0);
    top: -5px;
}
.list_buttons{
    position: absolute;
    top: 300px;
    display: flex;
    float: right;
    overflow-x: scroll;
    width: 100%;
}
</style>
