<template>
    <div class="BackGround_flexWarn" v-bind:class="{'BGfW_Fadein': btn_clicked_value == false, 'BGfW_Fadeout': btn_clicked_value == true}" @animationend="handleAnimationEnd()">
        <div class="flexWarn">
            <h1 class="title_flexWarn">確認</h1>
            <p class="WarnContent">{{ content }}</p>
            <p class="plusContent">{{ this.plusContent }}</p>
            <div class="checkBtn">
                <button  @click="confirm_clicked" class="confirm_flexWarn" >{{ confirm }}</button>
                <button @click="cancel_clicked" class="cancel_flexWarn">キャンセル</button>
            </div>
        </div>
    </div>
</template>

<style>
.plusContent{
    font-size: 1.05rem;
    text-align: center;
    color: red;
}

.WarnContent{
    font-size: 1.05rem;
    color: rgb(46, 46, 46);
    text-align: center;
    white-space: pre-wrap;
}

.BackGround_flexWarn{
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 100;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.788);
}

.BGfW_Fadein{
    animation: fadeIn 0.2s cubic-bezier(0.33, 1, 0.68, 1) forwards;
}

.BGfW_Fadeout{
    animation: fadeOut 0.2s cubic-bezier(0.33, 1, 0.68, 1) forwards;
}

.flexWarn{
    position: relative;
    width: 80%;
    height: fit-content;
    top: 230px;
    background: rgb(199, 199, 199);
    border-radius: 10px;
    margin: 0 auto;
    padding: 10px;
    box-shadow: 0px 6px 6px 0px rgba(255, 255, 255, 0.54);
    animation: zoomIn 0.5s cubic-bezier(0.25, 1, 0.5, 1) forwards;
}

.title_flexWarn{
    margin: 0;
    color: rgb(46, 46, 46);
    padding-left: 20px;
    font-size: 1.5rem;
}

.checkBtn{
    display: flex;
    justify-content: space-around;
}

.confirm_flexWarn ,.cancel_flexWarn{
    width: 45%;
    height: 40px;
    font-size: 1.1rem;
    color: white;
    border: none;
    border-radius: 5px;
}

.confirm_flexWarn{
    background-color: rgb(222, 56, 56);
}

.cancel_flexWarn{
    background: rgb(71, 71, 71);
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        display: block;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
        display: none;
    }
}

@keyframes zoomIn {
    0% {
        transform: scale(0.8);
        opacity: 0;
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}
</style>

<script>
export default{
    props: {
        content:String,
        confirm:String,
        btn_clicked_value: Boolean, //ボタンがクリックされたか
        selected_decision:Boolean,     //決定を選んだか
    },
    data() {
        return{
            confirm_clicked_number: 0,
            plusContent: ""
        };
    },
    methods:{
        cancel_clicked(){
            this.$emit('warning_btn_clicked',"cancel");
        },
        confirm_clicked() {
            if ( this.confirm_clicked_number < 4 ){
                this.plusContent = "削除まで："+String(4 - this.confirm_clicked_number) +"回"
                this.confirm_clicked_number += 1;
                console.log(this.confirm_clicked_number)
            } else {
                this.confirm_clicked_number = 0;
                this.$emit('warning_btn_clicked',"confirm");
            }
        },
        handleAnimationEnd() {
            if ( this.btn_clicked_value == true ){
                if ( this.selected_decision == true ){
                    this.$emit('warn_anim_end',"dalete_cache");
                } else {
                    this.$emit('warn_anim_end',"canceled");
                }
            }
        }
    }
}


</script>
