<template>
    <li class="input-component" :id=component_ids>
        <div class="data_area_base" id="data_area">
            <div class="data_area_bg_decoration" id="data_area_bg_decoration">
                <input type="checkbox" @click="checked()" class="check_button">
                <input type="text" @input="change_top" class="top-data" :id="load_top_id">
                <input type="text" @input="change_btm" class="btm-data" :id="load_btm_id">
                <p class="id_disp">component_id : {{ component_ids }}</p>
            </div>
            <div class="line_header" id="Drag_handle">
                <div class="line_body">
                    <span class="__Line _top"></span>
                    <span class="__Line _middle"></span>
                    <span class="__Line _bottom"></span>
                </div>
            </div>
        </div>
    </li>
</template>

<script>
import { smoothScrollToCenterWithMemory } from '../scripts/scroller';

export default {
    props: {
        component_ids: String,   //各四角のid
          load_top_id: String,   //上のinputの初期値
          load_btm_id: String,   //下のinputの初期値
           AutoScroll: Boolean,  //自動スクロールのフラグ
            ScrollRev: Boolean   //スクロール反転フラグ

    },
    methods: {
        change_top(e) {
            data_management('top_',this.component_ids,e.target.value);
        },
        change_btm(e){
            data_management('btm_',this.component_ids,e.target.value);
        },
        checked(){
            scroll_controller(this.AutoScroll,this.ScrollRev,this.component_ids)
        }
    },
};

/*チェックボックス高速化プログラム*/
var isTouch = false;

document.addEventListener('touchstart', function(e) {
    if (e.target.type === 'checkbox') {
        isTouch = true;
    }
}, { passive: true });

document.addEventListener('touchmove', function(e) {
    if (e.target.type === 'checkbox') {
        isTouch = false;
    }
}, { passive: true });

document.addEventListener('touchend', function(e) {
    if (e.target.type === 'checkbox') {
        if (isTouch) {
            e.target.click();
            e.preventDefault();
        }
    }
});

function data_management(pos, ids, data){
    localStorage.setItem( pos + ids , data);
    if (localStorage.getItem( pos + ids ) == "") {
        localStorage.removeItem( pos + ids )
    }
}

function scroll_controller(AutoScroll, ScrollRev, componentId) {
    let element;

    if (AutoScroll == true){
        if (ScrollRev == false){
            element = document.getElementById(componentId).nextElementSibling;
        } else {
            element = document.getElementById(componentId).previousSibling;
        }
        if (element !== null) {
            // スクロール先の要素が画面に収まるようにスクロール
            smoothScrollToCenterWithMemory( element, 100 )

        }
    }

}
</script>

<style>
    ul{
        padding: 0;
    }
    .input-component{
        padding-bottom: 30px;
    }
    .id_disp{
        position: relative;
        float: right;
        width: max-content;
        top: 10px;
        margin: 0;
        left: 100px;
    }
    .check_button{
        position: absolute;
        border-radius: 15px;
        appearance: none;
        z-index: 1;
        width: 224px;
        height: 224px;
    }
    .data_area_base{
        margin: 0 auto;
        z-index: -1;
        background-color: rgb(138, 138, 138);
        height: 224px;
        width: 350px;
        border-radius: 15px;
        text-align: right;
        transition: .3s;
    }
    .data_area_bg_decoration{
        display:flex;
        flex-flow: column;
        z-index: 0;
        position: absolute;
        width: 0px;
        height: 224px;
        background-color: #ff850387;
        transition-duration: .2s;
        border-radius: 15px;
    }
    .data_area_bg_decoration:has(.check_button:checked){
        width: 350px;
        transition-duration: .2s;
    }
    .top-data , .btm-data{
        z-index: 2;
        position: relative;
        margin-top: 10px;
        border-radius: 10px;
        width: 200px;
        height: 80px;
        font-size: 45px;
        text-align: center;
        border: none;
        left: 100px;
    }
    .__Line{
        position:relative;
        display:block;
        width: 15px;
        height: 2px;
        background-color:#ffffff;
        &._middle{
            top: 7px;
        }
        &._bottom{
            top: 14px;
            height: 2px;
        }
    }
    .line_body{
        position: relative;
        width: fit-content;
        height: 26px;
        top: 50%;
        right: 50%;
        transform: translate(50%,-50%);
        float: right;
    }
    .line_header{
        height: 224px;
        width: 30px;
        background: rgb(59, 59, 59);
        float: right;
        border-radius: 0px 15px 15px 0px;
    }
</style>